@layer reset {
  :where(:not(html, iframe, canvas, img, svg, video, audio, svg *, symbol *)) {
    all: unset;
    display: revert;
  }

  *, :before, :after {
    box-sizing: border-box;
  }

  html {
    -moz-text-size-adjust: none;
    text-size-adjust: none;
  }

  a, button {
    cursor: revert;
  }

  ol, ul, menu, summary {
    list-style: none;
  }

  ol {
    counter-reset: revert;
  }

  img {
    max-block-size: 100%;
    max-inline-size: 100%;
  }

  table {
    border-collapse: collapse;
  }

  input, textarea {
    -webkit-user-select: auto;
    user-select: auto;
  }

  textarea {
    white-space: revert;
  }

  meter {
    appearance: revert;
  }

  :where(pre) {
    all: revert;
    box-sizing: border-box;
  }

  ::placeholder {
    color: unset;
  }

  :where([hidden]) {
    display: none;
  }

  :where([contenteditable]:not([contenteditable="false"])) {
    -moz-user-modify: read-write;
    -webkit-user-modify: read-write;
    overflow-wrap: break-word;
    -webkit-line-break: after-white-space;
    -webkit-user-select: auto;
    user-select: auto;
  }

  :where([draggable="true"]) {
    -webkit-user-drag: element;
  }

  :where(dialog:modal) {
    all: revert;
    box-sizing: border-box;
  }

  ::-webkit-details-marker {
    display: none;
  }
}

@layer base {
  body {
    font-family: Noto Sans, Noto Sans JP, Helvetica Neue, Helvetica, Arial, sans-serif;
  }

  button {
    cursor: pointer;
  }
}

@layer layout {
  .ly_wrapper {
    padding: 1rem;
  }

  .ly_mv, .ly_info, .ly_card {
    margin-top: 1rem;
  }
}

:root {
  --color1: #ffa725;
  --color2: #fff5e4;
  --color3: #c1d8c3;
  --color4: #6a9c89;
  --color-white: #fff;
  --color-red: red;
}

@layer block {
  .bl_header_title {
    font-size: clamp(1rem, 4vw, 3rem);
  }

  :is(.bl_header_title, .bl_header_subTitle, .bl_header_sectionTitle) {
    text-wrap: balance;
    font-weight: bold;
  }

  .bl_info {
    background-color: var(--color3);
    padding: 1em;
  }

  .bl_info_inner {
    width: fit-content;
    margin-inline: auto;
  }

  .bl_info_img {
    aspect-ratio: 1;
    background-color: var(--color1);
    width: 100px;
  }

  .bl_card_list {
    grid-template-columns: repeat(auto-fit, minmax(min(300px, 100%), 1fr));
    gap: 1rem;
    width: 100%;
    display: grid;
  }

  .bl_card_item {
    grid-template-rows: subgrid;
    background-color: var(--color3);
    grid-row: span 4;
    row-gap: .2em;
    width: 100%;
    padding: 1em;
    display: grid;
    position: relative;
  }

  .bl_card_item:has(.bl_card_button) {
    border: 5px solid var(--color1);
  }

  .bl_card_title {
    container-type: inline-size;
  }

  .bl_card_title span {
    display: block;
  }

  @container (width > 380px) {
    .bl_card_title span {
      color: var(--color-red);
      font-size: 1.5rem;
    }
  }

  .bl_card_img {
    width: 100%;
    height: auto;
  }

  .bl_card_button {
    width: 100%;
    height: 3em;
    color: var(--color-white);
    background-color: var(--color1);
    border: 3px solid;
    border-radius: 100vmax;
    justify-content: center;
    align-items: center;
    margin-top: 1em;
    display: inline-flex;
  }

  @media (any-hover: hover) {
    .bl_card_button:hover {
      color: var(--color-red);
    }
  }
}

@layer unique {
  .un_mv {
    background-color: var(--color3);
    height: 30vh;
    position: relative;
  }

  .un_mv_inner {
    background-color: var(--color2);
    position: absolute;
    inset: 10px;
  }

  .un_mv_figure_wrapper {
    background-color: var(--color4);
    place-content: center;
    width: 100%;
    height: 100%;
    display: grid;
  }

  .un_mv_figure {
    background-color: var(--color1);
    clip-path: polygon(50% 0%, 100% 100%, 0% 100%);
    width: 100px;
    height: 86.6px;
  }
}

@layer utilities {
  .pt-1rem {
    padding-top: 1rem;
  }
}
